body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

body, html, * {
  box-sizing: border-box;
  font-size: 20px; }

.container {
  text-align: center;
  height: 100vh;
  width: 100%;
  padding: 50px 30px; }

img {
  width: 100%;
  max-height: 250px; }

.item .item-info {
  display: flex;
  justify-content: space-between; }
  .item .item-info span {
    font-size: 15px; }

.item .detail .title {
  font-size: 1.3em;
  font-weight: bold; }

.item .detail span {
  word-break: break-all; }

.item .action {
  display: flex;
  margin-top: 10px; }
  .item .action button {
    display: flex;
    margin-right: 10px;
    align-items: center;
    justify-content: center;
    border: 1px solid gainsboro;
    padding: 5px;
    color: grey;
    background: white;
    border-radius: 3px; }
    .item .action button.w-100 {
      width: 100%; }
    .item .action button.red {
      color: red; }

.dialog-header {
  display: flex;
  justify-content: space-between; }

.box-info {
  display: flex;
  align-items: flex-start;
  flex-direction: column;
  border: 1px solid gainsboro;
  border-radius: 4px;
  margin: 10px 0;
  padding: 3px 5px; }
  .box-info .title {
    font-size: 15px; }
  .box-info input, .box-info textarea {
    border: none;
    width: 100%; }
    .box-info input:focus, .box-info textarea:focus {
      outline: none; }

.dialog-action {
  display: flex;
  align-self: center;
  background: #784CC0;
  color: white;
  border: 1px solid white;
  border-radius: 4px;
  font-size: 30px; }

.search-container {
  text-align: left; }
  .search-container .title {
    font-size: 1.7rem; }
  .search-container input {
    padding: 10px;
    width: 100%;
    border: 1px solid gainsboro;
    border-radius: 5px; }

.item-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between; }
  .item-container .item {
    flex-basis: 30%;
    margin: 20px 0; }

.container {
  text-align: center;
  height: 100vh;
  width: 100%;
  padding: 50px 30px; }

.App-logo {
  height: 40vmin; }

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 25px auto; }
  .header span {
    font-size: 70px;
    color: #a2a2a2; }
  .header button {
    background: #784CC0;
    color: white;
    border: 1px solid white;
    border-radius: 4px;
    font-size: 30px; }

